import { saleArrs } from '../../mock/api-class-goods.js';
export default {
	name: 'goods-list',
	props: {
		// 是否有特殊处理事件 默认值： 否
		eventHandle: {
			type: String,
			default: ''
		},
		hot: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			saleArrs,
			baseUrl: 'http://39.108.65.247:8087',
			num: 0
		}
	},
	methods: {
		goodNumControl(tag) {
			switch (tag) {
				case 'reduce': {
					this.num = Math.max(0, --this.num);
					break;
				}
				case 'plus': {
					this.num = Math.min(200, ++this.num);
					break;
				}
				default: break;
			}
		},
		productPlus() {
			this.num = 1;
		},
		skipTo() {
			uni.navigateTo({
			    url: "/pages/other/good-detail"
			});
		}
	}
}
